//mixin 
import Vue from "vue";
import Component from "vue-class-component";

// map
const map: { [key: string]: string } = {
  "tag name duplicated": "",
};

@Component
export class TagHelper extends Vue {
  createTag() {
    this.$prompt("", "", {
      confirmButtonText: "",
      cancelButtonText: "",
    })
      .then(({ value }: any) => {
        if (value.length > 5) {
          return window.alert("5");
        }
        this.$store.commit("createTag", value);
        if (this.$store.state.createTagError) {
          //
          window.alert(
            map[this.$store.state.createTagError.message] || ""
          );
        }
      }) //
      .catch(() => {
        this.$message({
          type: "info",
          message: "",
        });
      });

    // let name = window.prompt("");
    // if (!name) {
    //   return window.alert("");
    // }
    // this.$store.commit("createTag", name);
    // //
    // if (this.$store.state.createTagError) {
    //   window.alert(map[this.$store.state.createTagError.message] || "");
    // }
  }
}

export default TagHelper;
