















































import Layout from "@/components/Layout.vue";
import Vue from "vue";
import { Component } from "vue-property-decorator";
import Tabs from "@/components/Tabs.vue";
import recordTypeList from "@/constants/recordTypeList";
import dayjs from "dayjs";
import clone from "@/lib/clone";
import Chart from "@/components/Chart.vue";
import _ from "lodash";
import day from "dayjs";
import Icon from "@/components/Icon.vue";

@Component({ components: { Layout, Tabs, Chart, Icon } })
export default class Statistics extends Vue {
  flag = "detail";
  get changeFlag() {
    return (this.$store.state as RootState).flag;
  }
  show1() {
    this.flag = "detail";
  }
  show2() {
    this.flag = "chart";
  }

  //chartoptions
  get chartKeyValue() {
    const today = new Date();
    const array = [];
    //amount
    for (let i = 0; i <= 6; i++) {
      //  - i  
      const dateString = day(today).subtract(i, "day").format("YYYY-MM-DD");
      //  createdAtdataString
      // console.log(this.groupList);

      const found = _.find(this.groupList, { title: dateString });

      // array    0
      array.push({
        date: dateString,
        value: found ? found.total : 0,
      });
    }
    //lodashmap   createdAtamount
    // pick   
    // console.log(this.recordList.map((r) => _.pick(r, ["createdAt", "amount"])));
    //     
    array.sort((a, b) => {
      if (a.date > b.date) {
        return 1;
      } else if (a.date === b.date) {
        return 0;
      } else {
        return -1;
      }
    });
    return array;
  }

  get chartOptions() {
    const keys = this.chartKeyValue.map((item) => item.date);
    const values = this.chartKeyValue.map((item) => item.value);

    return {
      grid: {
        left: 0,
        right: 0,
      },

      xAxis: {
        axisTick: { alignWithLabel: true },
        axisLine: { lineStyle: { color: "#666" } },
        type: "category",
        data: keys,
        axisLabel: {
          formatter: function (value: string, index: number) {
            //  5
            return value.substr(5);
          },
        },
      },
      yAxis: {
        type: "value",
        show: false,
      },
      series: [
        {
          symbol: "circle",
          symbolSize: 10,
          itemStyle: { color: "#666" },
          data: values,
          type: "line",
        },
      ],
      tooltip: {
        triggerOn: "click",
        position: "top",
        formatter: "{c}",
      },
    };
  }

  mounted() {
    //
    const div = this.$refs.chartWrapper as HTMLDivElement;
    if (div) {
      div.scrollLeft = div.scrollWidth;
    }
  }

  //
  tagString(tags: Tag[]) {
    return tags.length === 0 ? "" : tags.map((t) => t.name).join("&");
  }
  //  
  beautify(string: string) {
    //dayapi    now
    const day = dayjs(string);
    const now = dayjs();
    //'day' day ''
    if (day.isSame(now, "day")) {
      return "";
      //
    } else if (day.isSame(now.subtract(1, "day"), "day")) {
      return "";
    } else if (day.isSame(now.subtract(2, "day"), "day")) {
      return "";
    } else if (day.isSame(now, "year")) {
      return day.format("MD");
    } else {
      return day.format("YYYYMD");
    }
  }
  //

  get recordList() {
    //recordList   vuex  
    return (this.$store.state as RootState).recordList;
  }
  //
  get groupList() {
    //result [{title,items},{title,items},...]
    const { recordList } = this;

    //  filter(+-) sort 
    const newList = clone(recordList)
      .filter((r) => r.type === this.type)
      .sort(
        (a, b) => dayjs(b.createdAt).valueOf() - dayjs(a.createdAt).valueOf()
      );
    if (newList.length === 0) {
      return [];
    }
    // console.log(newList);
    type Result = { title: string; total?: number; items: RecordItem[] }[];
    const result: Result = [
      {
        title: dayjs(newList[0].createdAt).format("YYYY-MM-DD"),
        items: [newList[0]],
      },
    ];
    for (let i = 1; i < newList.length; i++) {
      const current = newList[i];
      const last = result[result.length - 1];
      if (dayjs(last.title).isSame(dayjs(current.createdAt), "day")) {
        last.items.push(current);
      } else {
        result.push({
          title: dayjs(current.createdAt).format("YYYY-MM-DD"),
          items: [current],
        });
      }
    }
    //
    result.map((group) => {
      group.total = group.items.reduce((sum, item) => sum + item.amount, 0);
    });
    return result;
  }

  beforeCreate() {
    //
    this.$store.commit("fetchRecords");
  }

  //
  type = "-";
  recordTypeList = recordTypeList;
}
